"use client";
import React, { useState } from 'react';

const TodoList = () => {
  const [todos, setTodos] = useState([
    { id: 1, text: 'Buy milk', completed: false },
    { id: 2, text: 'Walk the dog', completed: false },
    { id: 3, text: 'Do laundry', completed: false },
  ]);

  const [newTodo, setNewTodo] = useState('');

  const handleAddTodo = () => {
    if (!newTodo.trim()) return;
    setTodos([...todos, { id: todos.length + 1, text: newTodo, completed: false }]);
    setNewTodo('');
  };

  const handleToggleCompleted = (id) => {
    setTodos(todos.map((todo) => (todo.id === id ? { ...todo, completed: !todo.completed } : todo)));
  };

  const handleDeleteTodo = (id) => {
    setTodos(todos.filter((todo) => todo.id !== id));
  };

  const handleClearAll = () => {
    setTodos([]);
  };

  return (
    <div className="max-w-md mx-auto p-8 bg-gradient-to-b from-indigo-100 via-purple-100 to-pink-100 rounded-3xl shadow-xl transition-all duration-300 ease-in-out transform hover:scale-105">
      <h1 className="text-5xl font-extrabold text-purple-800 mb-8 text-center animate-pulse">To-Do List</h1>
      <form onSubmit={(e) => e.preventDefault()} className="flex mb-6">
        <input
          type="text"
          value={newTodo}
          onChange={(e) => setNewTodo(e.target.value)}
          placeholder="Add a new task"
          className="w-full p-4 pl-10 text-lg text-gray-800 bg-white rounded-lg shadow-inner focus:outline-none focus:ring-4 focus:ring-purple-500"
        />
        <button
          type="submit"
          onClick={handleAddTodo}
          className="ml-2 p-4 text-lg font-semibold text-white bg-purple-600 rounded-lg shadow-lg hover:bg-purple-700 focus:outline-none focus:ring-4 focus:ring-purple-500 transform hover:scale-105"
        >
          Add
        </button>
      </form>
      <ul className="space-y-4">
        {todos.map((todo) => (
          <li
            key={todo.id}
            className="flex items-center p-4 bg-white rounded-lg shadow-md border-l-4 transition-all duration-200 ease-in-out transform hover:scale-105 hover:shadow-lg"
            style={{ borderColor: todo.completed ? '#4caf50' : '#9c27b0' }}
          >
            <input
              type="checkbox"
              checked={todo.completed}
              onChange={() => handleToggleCompleted(todo.id)}
              className="mr-4 h-6 w-6 text-purple-600 focus:ring-purple-500"
            />
            <span
              className={`text-lg font-medium transition-all duration-300 ease-in-out ${
                todo.completed ? 'text-gray-400 line-through' : 'text-gray-800'
              }`}
            >
              {todo.text}
            </span>
            <button
              onClick={() => handleDeleteTodo(todo.id)}
              className="ml-auto p-2 text-red-600 bg-red-50 rounded-lg hover:bg-red-100 focus:outline-none focus:ring-2 focus:ring-red-500 transform hover:scale-110"
            >
              Delete
            </button>
          </li>
        ))}
      </ul>
      <div className="flex justify-center mt-8">
        <button
          onClick={handleClearAll}
          className="p-4 text-lg font-semibold text-white bg-red-500 rounded-lg shadow-lg hover:bg-red-600 focus:outline-none focus:ring-4 focus:ring-red-400 transform hover:scale-105"
        >
          Clear All
        </button>
      </div>
    </div>
  );
};

export default TodoList;
